@import 'variables.scss';
@import "svg-sprite";
@import "png-sprite";

.main {
  width: 1200px;
  padding: 0 40px;
  margin: 0 auto;

  .header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    background-color: #FFFFFF;
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);

    &_adress {
      display: flex;
      flex-direction: row;
      align-items: center;

      &_name {
        padding-right: 25px;
        display: flex;
        flex-direction: row;
        align-items: center;
        &_location {
          @include sp__location;
          margin-right: 10px;
        }
        span {
          color: #7F7873;
          font-family: $font_ubuntu;
          font-size: 14px;
          line-height: 17px;
        }
      }
      &_time {
        padding-right: 30px;
        display: flex;
        flex-direction: row;
        align-items: center;
       &_logo {
         @include sp__time;
         margin-right: 10px;
       }
        span {
          color: #7F7873;
          font-family: $font_ubuntu;
          font-size: 14px;
          line-height: 17px;
        }
      }
      &_language {
        display: flex;
        flex-direction: row;
        align-items: center;
        ul {
          background: url(../images/language.png) no-repeat;
          width: 22px;
          height: 14px;

          li {
            display: none;
          }
        }

        &_triangle {
          @include sp__Triangle2;
          height: 6px;
          width: 8px;
          margin-left: 7px;
        }
      }

    }
    &_tabs {
      display: flex;
      flex-direction: row;
      &_search {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding-right: 20px;
      &_logo {
        @include sp__search_small;
        margin-right: 10px
      }
        input {
          height: 17px;
          width: 80px;
          color: #7F7873;
          font-family: $font_ubuntu;
          font-size: 14px;
          line-height: 17px;
          border: none;
          outline: none;
        }
      }
      &_login {
        display: flex;
        flex-direction: row;
        align-items: center;
        padding-right: 30px;
       &_logo {
         @include sp__user_small;
         margin-right: 10px
       }
        span {
          height: 17px;
          width: 32px;
          color: #231F20;
          font-family: $font_ubuntu;
          font-size: 14px;
          line-height: 17px;
        }
      }
      &_bucket {
        display: flex;
        flex-direction: row;
        align-items: center;
        &_logo {
          @include sp__basket_small;
          margin-right: 10px;
        }
        span {
          height: 17px;
          width: 55px;
          color: #231F20;
          font-family: $font_ubuntu;
          font-size: 14px;
          line-height: 17px;
        }
      }

    }
  }
  .menu-tabs {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 35px 0 35px 0;
    &_menu {
      display: flex;
      flex-direction: row;
      font-family: $font_condensed;
      font-size: 16px;
      color: #231F20;
      font-weight: 600;
      line-height: 20px;
      text-align: center;
      text-transform: uppercase;
      &_item {
        padding-right: 40px;

        &:last-child {
          padding-right: 0;
        }
      }
    }
    &_logo {
      @include sp__Logo;
    }
    &_addit {
      display: flex;
      flex-direction: row;
      &_delivery {
        display: flex;
        flex-direction: row;
        padding-right: 30px;
        font-family: $font_ubuntu;
        font-size: 14px;
        line-height: 17px;
        text-align: right;
        color: #7F7873;
        &_item {
          padding-right: 20px;

          &:last-child {
            padding-right: 0;
          }

        }
      }
      &_socials {
        display: flex;
        flex-direction: row;
        align-items: center;
        &_item {
          margin-right: 18px;

          &:last-child {
            margin-right: 0;
          }
        }
        .vk {
          @include sp__vk;
        }
        .facebook {
          @include sp__facebook;
        }
        .twitter {
          @include sp__twitter;
        }
        .instagram {
          @include sp__instagram;
        }
        .pinterest {
          @include sp__pinterest;
        }
      }
    }
  }
  .slider {
    //padding: 0 40px 0 40px;
    position: relative;
    //img {
    //  width: 100%;
    //  height: 500px;
    //}
    .owl-carousel {
      .owl-nav {
        position: absolute;
        top: 50%;
        width: 100%;
        transform: translateY(-50%);
        background: none;

        .owl-prev {
          display: inline-block;
          float: left;
          background: none;
          width: 13px;
          height: 22px;
        }

        .owl-next {
          display: inline-block;
          float: right;
          background: none;
          width: 13px;
          height: 22px;
          transform: rotate(180deg);
          -webkit-transform: rotate(180deg);

        }
      }
      //width: 100%;
      //height: 300px;
    }

    &-footer {
      display: flex;
      flex-direction: row;
      position: relative;
      &_delivery {
        height: 170px;
        width: 600px;
        background-color: #F7F6F2;
        padding-bottom: 10px;
        font-family: $font_ubuntu;
        position: relative;
        &_main-text {
          color: #EBE8DD;
          font-size: 90px;
          font-weight: bold;
          line-height: 90px;
          text-align: center;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
        &_secondary-text {
          position: absolute;
          top: 57%;
          left: 50%;
          transform: translate(-50%, -57%);
          width: 287px;
          color: #231F20;
          font-size: 18px;
          line-height: 24px;
          text-align: center;
        }
        &_secondary-text_preorder {
          position: absolute;
          top: 57%;
          left: 50%;
          transform: translate(-50%, -57%);
          width: 227px;
          color: #231F20;
          font-size: 18px;
          line-height: 24px;
          text-align: center;
        }
        &_moreinfo-text {
          display: flex;
          width: 200px;
          align-items: center;
          top: 90%;
          position: absolute;
          left: 55%;
          transform: translate(-50%, -50%);

          &_left-dash, &_right-dash {
            height: 2px;
            width: 21px;
            background-color: #CBBC9D;
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
          }
          &_text {
            margin: 0 10px;
            color: #231F20;
            font-size: 16px;
            font-weight: bold;
            line-height: 16px;
            text-align: center;
          }
        }
        &_img {
          position: absolute;
          bottom: 0;
          right: 65px;
          &_right {
            position: absolute;
            top: 0;
            right: 133px;
          }
        }
      }
      &_rectangle {
        background: url(img/Rectangle2.png) no-repeat;
        height: 2px;
        width: 101px;
        transform: rotate(135deg);
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
        position: absolute;
        top: 50%;
        left: 46%;
      }
    }
  }
  .content_menu {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 37px 0;
    &_about {
      @include sp__Logo1;
      //font-family: $font_underground;
      //
      //&_logo-text {
      //  color: #231F20;
      //  font-size: 22px;
      //  font-weight: 500;
      //  line-height: 27px;
      //}
      //&_text {
      //  color: #C9BA9B;
      //  font-size: 16px;
      //  font-weight: 500;
      //  line-height: 19px;
      //  text-align: right;
      //}
    }
    &_other {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      font-family: $font_condensed;
      &_item {
        color: #231F20;
        font-size: 16px;
        line-height: 18px;
        margin-right: 40px;
      }
      &_search {
        @include sp__Search;
        margin-right: 40px;
      }
      &_basket {
        @include sp__basket;

      }
      &_personality {
        @include sp__User;
        margin-right: 40px;
      }
    }
  }
  .instagram_content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 40px 0 0 0;
    font-family: $font_ubuntu;

    &_item {
      width: 263px;
      height: 263px;
      position: relative;

      &_main-text {
        color: #7F7873;
        font-size: 26px;
        font-weight: 500;
        line-height: 30px;
        padding-top: 76px;
        left: 50%;
        position: absolute;
        transform: translateX(-50%);

      }
    }
    .slider-footer_delivery_moreinfo-text {
      width: 216px;
      top: 55%;
      left: 50%;
      transform: translate(-50%, -55%)
    }
  }
  .main_map {
    padding-top: 40px;
    position: relative;

    &_buttons {
      position: absolute;
      width: 187px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      top: 50%;
      left: 40px;
      transform: translateY(-50%);
      font-family: $font_ubuntu;

      &_button {
        margin-bottom: 10px;
        border-radius: 2px;
        cursor: pointer;
        width: 147px;

        &_text {
          padding: 17px 42px 17px 20px;
          background-color: rgba(35, 31, 32, 0.8);
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
          color: #FFFFFF;

        }

      }
      .active {
        width: 187px;

        .main_map_buttons_button_text {
          padding: 17px 57px 17px 20px;
          background-color: #FFFFFF;
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
          color: #1A1718;
        }
      }
    }
  }
  .subscribe_content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-bottom: 1px solid #D9D5D2;
    padding: 33px 0;

    &_socials {
      display: flex;
      flex-direction: row;
      justify-content: space-between;

      &_vk {
        @include sp__vk_sircle;
        margin-right: 10px;
      }
      &_fb {
        @include sp__facebook_sircle;
        margin-right: 10px;
      }
      &_twit {
        @include sp__twitter_sircle;
        margin-right: 10px;
      }
      &_inst {
        @include sp__instagram_sircle;
        margin-right: 10px;
      }
      &_pint {
        @include sp__pinterest_sircle
      }
    }
    &_main {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      font-family: $font_ubuntu;
      align-items: center;
      &_text {
          color: #1A1718;
          font-size: 16px;
          line-height: 24px;
          margin-right: 10px;
      }
      &_form {
        display: flex;
        flex-direction: row;
        justify-content: center;
        input[type=email] {
            border: 1px solid #CCC8C4;
            border-radius: 2px;
            background-color: #FFFFFF;
            padding: 10px;
            margin-right: 10px;
            font-size: 16px;
        }
        button {
            height: 44px;
            width: 147px;
            border-radius: 2px;
            background-color: #332C26;
            border: none;
            color: #FFFFFF;
        }
      }
    }
  }
  .footer {
    display: flex;
    width: 100%;
    flex-direction: column;
    background-color: #FFFFFF;
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.1);
    &_main {
      display: flex;
      flex-direction: row;
      padding: 74px 0 32px;
      width: 100%;
      justify-content: space-between;


      &_menu {
        display: flex;
        flex-direction: column;
        width: 297px;

        &_header {
          display: flex;
          flex-direction: row;
          justify-content: space-between;

          &_item {
            font-size: 16px;
            color: #231F20;
            line-height: 16px;
            font-family: $font_ubuntu;
          }
        }

        &_items {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          padding: 20px 12px 0 0;
          &_item {
            font-size: 13px;
            color: #7F7873;
            line-height: 13px;
            font-family: $font_ubuntu;
          }

        }

      }

      &_contacts {
        display: flex;
        flex-direction: column;

        &_header {
          font-size: 16px;
          color: #231F20;
          line-height: 16px;
          font-family: $font_ubuntu;
          padding-bottom: 15px;

        }

        &_blocks {
          display: flex;
          flex-direction: row;

          &_items {

            &_item {
              font-size: 13px;
              color: #1A1718;
              line-height: 26px;
              font-family: $font_ubuntu;
            }

            &:first-child {
              padding-right: 40px;
            }
          }
        }


      }
    }

    &_info {
      display: flex;
      flex-direction: row;
      align-items: center;
      width: 100%;
      padding: 0 0 32px;

      &_copyright {
        font-size: 13px;
        color: #7F7873;
        line-height: 13px;
        font-family: $font_ubuntu;
        font-weight: 500;
        padding-right: 81px;
        width: 249px;

      }

      &_payments {
        display: flex;
        flex-direction: row;
        margin-right: 156px;
        align-items: center;

        &_visa {
          @include sp__Visa;
          margin-right: 19px;
        }
        &_american-express {
          @include sp__AMEX;
          margin-right: 21px;
        }

        &_master-card {
          @include sp__MasterCard;
          margin-right: 24px;

        }

        &_paypal {
          @include sp__PayPal;
        }

      }

      &_made-by {
        display: flex;
        flex-direction: row;

        &_text {
          font-size: 13px;
          color: #1A1718;
          line-height: 24px;
          font-family: $font_ubuntu;
          padding-right: 10px;
        }

        &_icon {
          @include sp__decus;
        }

      }
    }
  }
}

.main_content {
  background-color: #F7F6F2;
  width: 1280px;
  margin: 0 auto;

  .content_corners {
    padding: 40px;
    margin: 0 auto;
    max-width: 1200px;
    display: flex;
    flex-direction: column;
    align-items: center;

    &_title {
      color: #231F20;
      font-family: $font_condensed;
      font-size: 30px;
      line-height: 30px;
      text-align: center;
    }
    &_items {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
      min-width: 1200px;
      &_item {
        width: 380px;
        border: 1px solid #E6E3E1;
        border-radius: 0 0 4px 4px;
        background-color: #FFFFFF;
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
        margin: 30px 0;
        position: relative;
        &_img {
          position: relative;
          .logo {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }

        &_content {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          font-family: $font_ubuntu;
          padding: 15px 20px 10px;

          &_name {
            color: #231F20;
            font-size: 18px;
            line-height: 21px;
          }
          &_time {
            color: #867F7B;
            font-size: 13px;
            line-height: 13px;
            padding: 4px 7px;;
