            background-color: #F7F6F2;
            box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
          }
          &-kitchen {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            color: #7F7873;
            font-size: 13px;
            line-height: 13px;
            padding: 10px 133px 20px 20px;
            align-items: center;
            font-family: $font_ubuntu;

            &_item {

            }
            &_delimiter {
              @include sp__rectangle;
            }
          }
        }
        &-hover {
          font-family: $font_ubuntu;
          &_main {
            background-color: rgba(0,0,0,0.8);
            padding: 55px 30px 55px;
            position: absolute;
            top: 0;

            &_text {
              font-size: 18px;
              font-weight: 500;
              line-height: 20px;
              color: #FFFFFF;
              width: 319px;
            }
            &_checkin {
              display: flex;
              flex-direction: row;
              justify-content: space-between;
              color: #FFFFFF;
              margin-top: 20px;
              align-items: center;
              font-family: $font_ubuntu;
              &_item {
                background-color: #1A1718;
                padding: 5px 10px;
                margin-right: 5px;
                span {
                    color: #FFFFFF;
                    font-size: 13px;
                    line-height: 19px;
                    text-align: center;
                }

                &:last-child {
                  margin-right: 0;
                }
              }
            }
          }
        }
      }

    }
    &_moreinfo-text {
      display: flex;
      align-items: center;
      font-family: $font_ubuntu;
      text-transform: uppercase;

      &_left-dash, &_right-dash {
        height: 2px;
        width: 21px;
        background-color: #CBBC9D;
        box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
      }
      &_text {
        margin: 0 10px;
        color: #231F20;
        font-size: 16px;
        font-weight: bold;
        line-height: 16px;
        text-align: center;
      }
    }
  }

}

