.about_content {
  position: relative;
  width: 1280px;
  margin: 0 auto;
  //flex-direction: column;
  &_title {
    position: relative;
    font-family: $font_ubuntu;
    font-weight: 500;
    font-size: 40px;
    z-index: 1;
    line-height: 44px;
    margin: 0 auto;
    width: 640px;
    padding: 50px 0 0 0;

  }
  &_footer-grey {
    position: absolute;
    height: 273px;
    width: 61px;
