$font_ubuntu: Ubuntu, sans-serif;
$font_underground: P22 Underground CY Pro;
$font_condensed: UbuntuCondensed, sans-serif;

@font-face {
  font-family: 'Ubuntu Light';
  src: url('../../fonts/Ubuntu/UbuntuLight.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  unicode-range: U+0020-2074;
}

@font-face {
  font-family: 'Ubuntu Light';
  src: url('../../fonts/Ubuntu/UbuntuLightItalic.ttf') format('truetype');
  font-weight: 300;
  font-style: italic;
  font-stretch: normal;
  unicode-range: U+0020-2212;
}

